import { Injectable } from '@angular/core';

import {
    IBreadcrumbItem
} from '../models/breadcrumb.interface';
import { BehaviorSubject } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class BreadcrumbClipboardService {

    readonly clipboard: BehaviorSubject<IBreadcrumbItem[]>;

    constructor() {
        this.clipboard = new BehaviorSubject([]);
    }

    paste(items: IBreadcrumbItem[]) {
        this.clipboard.next(items);
    }
}
